.new__container {
  position: relative;
  margin-bottom: 115px;
  min-width: 0;

  @media (max-width: $breakpoint-tablet) {
    margin-bottom: 63px;
  }

  @media (max-width: $breakpoint-mobile) {
    margin-bottom: 56px;
  }

  &--product {
    margin-bottom: 127px;

    @media (max-width: $breakpoint-tablet) {
      margin-bottom: 73px;
    }

    @media (max-width: $breakpoint-mobile) {
      margin-bottom: 65px;
    }
  }
}

.new h2 {
  @include font-source(34px, bold, 44px, $main-black);
  @include clear(23px);
  text-align: center;

  @media (max-width: $breakpoint-tablet) {
    margin-bottom: 20px;
  }
}

.new__link {
  margin: 0 auto;
  margin-bottom: 59px;
  display: block;
  width: 81px;
  color: $main-black;
  text-align: center;
  border-bottom: 1px solid $brown;

  @media (max-width: $breakpoint-tablet) {
    margin-bottom: 54px;
  }

  @media (max-width: $breakpoint-mobile) {
    margin-bottom: 42px;
  }
}

.new__swiper {
  width: 100%;
  height: 100%;
  margin-bottom: 51px;

  @media (max-width: $breakpoint-tablet) {
    margin-bottom: 39px;
  }

  @media (max-width: $breakpoint-mobile) {
    margin-bottom: 32px;
  }
}

.new__list {
  @include no-list;
  display: flex;
}

.new__item {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 23%;
  height: 100%;
  margin-right: 30px;

  &:last-child {
    margin-right: 0;
  }

  @media (max-width: $breakpoint-tablet) {
    width: 50%;
  }

  & img {
    width: 100%;
    height: auto;
  }

  & p {
    margin-top: 17px;
    margin-bottom: 6px;

    @media (max-width: $breakpoint-mobile) {
      margin-top: 14px;
      margin-bottom: 2px;
    }
  }

  & span {
    font-size: 14px;
    font-weight: bold;
    line-height: 18px;
  }
}


.new__link-product {
  color: $main-black;
  border: 1px solid transparent;
  cursor: pointer;

  &:hover,
  &:focus {
    border: 1px solid $brown;
  }

  &:active {
    opacity: 0.7;
  }
}

// Previous
.new__btn {
  position: absolute;
  top: 39px;
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;

  & svg {
    width: 29px;
    height: 9px;
    color: $brown;

    @media (max-width: $breakpoint-tablet) {
      width: 39px;
      height: 25px;
    }
  }

  &:hover svg,
  &:focus svg {
    transform: scale(1.1);
  }

  &:active svg {
    color: $main-black;
  }
}

.new__prev-btn {
  left: 13px;

  @media (max-width: $breakpoint-tablet) {
    left: 44px;
  }
}

.new__next-btn {
  right: 14px;

  @media (max-width: $breakpoint-tablet) {
    right: 44px;
  }
}

.new__next-btn svg,
.new__next-btn:hover svg,
.new__next-btn:focus svg,
.new__next-btn:active svg {
  transform: rotate(180deg);
}

// Pagination
.new__pagination {
  text-align: center;
}

.new__pagination .swiper-pagination-bullet {
  width: 20px;
  height: 20px;
  margin-right: 2px;

  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  text-align: center;
  color: $grey;
  border: none;
  padding: 0;
  opacity: 1;
  background-color: transparent;
  cursor: pointer;

  &:last-child {
    margin-right: 0;
  }

  &:hover,
  &:focus {
    color: $brown;
  }

  &:active {
    color: $main-black;
  }
}

.new__pagination .swiper-pagination-bullet-active {
  color: $main-black;
  background: transparent;
}

@media (max-width: $breakpoint-mobile) {
  .new__container--product .new__btn {
    display: none;
  }
}
