.catalog {
  position: relative;

  &::before {
    @include pseudo(" ", absolute, 100%, 1px);
    bottom: 130px;
    left: 0;
    background-color: $light-grey;
  }

  @media (max-width: $breakpoint-mobile) {
    @include no-outline;
  }
}

.catalog__container {
  display: flex;
  justify-content: space-between;

  @media (max-width: $breakpoint-tablet) {
    flex-direction: column;
  }
}

.catalog__left {
  display: flex;
  flex-direction: column;
  // width: 75%;
  width: 870px;

  @media (max-width: $breakpoint-tablet) {
    width: 100%;
  }
}

.catalog__filter {
  display: flex;
  flex-direction: column;
  width: 250px;
  margin-right: 50px;

  @media (max-width: $breakpoint-tablet) {
    display: none;
  }
}

.catalog__filter-btn {
  display: none;

  @media (max-width: $breakpoint-tablet) {
    @include main-font-source(16px, normal, 21px, $white);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 68px;
    background-color: $brown;
    border: 1px solid $brown;
    margin-bottom: 50px;
    cursor: pointer;

    &:hover,
    &:focus {
      border: 1px solid $brown;
    }

    &:active {
      opacity: 0.7;
    }
  }

  @media (max-width: $breakpoint-mobile) {
    margin-bottom: 40px;
  }
}

.catalog__cards {
  & ul {
    @include no-list;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-between;

    @media (max-width: $breakpoint-mobile) {
      justify-content: center;
    }
  }

  & li {
    display: flex;
    flex-direction: column;
    width: 31%;
    max-width: 270px;
    height: auto;
    margin-right: 15px;
    margin-bottom: 33px;
    text-align: center;

    &:nth-child(3n) {
      margin-right: 0;
    }

    @media (max-width: $breakpoint-tablet) {
      margin-bottom: 32px;
    }

    @media (max-width: $breakpoint-mobile) {
      width: 45%;
      margin-right: 0;
      margin-bottom: 22px;

      &:not(:nth-child(2n)) {
        margin-right: 27px;
      }
    }
  }

  & img {
    width: 100%;
    height: auto;
  }

  & p {
    margin-top: 17px;
    margin-bottom: 5px;

    @media (max-width: $breakpoint-tablet) {
      margin-top: 13px;
      margin-bottom: 5px;
    }

    @media (max-width: $breakpoint-mobile) {
      text-align: center;
      margin-top: 11px;
    }
  }

  & span {
    font-size: 14px;
    font-weight: bold;
    line-height: 18px;
  }

  & a {
    color: $main-black;
    border: 1px solid transparent;
    cursor: pointer;

    &:hover,
    &:focus {
      border: 1px solid $brown;
    }

    &:active {
      opacity: 0.7;
    }
  }
}
