.faq__container {
  margin-bottom: 98px;

  @media (max-width: $breakpoint-tablet) {
    margin-bottom: 63px;
  }

  @media (max-width: $breakpoint-mobile) {
    margin-bottom: 49px;
  }
}

.faq h2 {
  @include font-source(34px, bold, 44px, $main-black);
  @include clear(48px);
  letter-spacing: 0.03em;
  text-align: center;

  @media (max-width: $breakpoint-mobile) {
    margin-bottom: 27px;
  }
}

.faq__list {
  @include no-list;
  padding: 0 100px;

  @media (max-width: $breakpoint-tablet) {
    padding: 0;
  }
}

.faq__item {
  padding-top: 42px;
  padding-bottom: 41px;
  padding-right: 38px;
  padding-left: 38px;
  border: 1px solid $brown;
  cursor: pointer;

  &:hover,
  &:focus {
    background-color: $beige;
  }

  &:not(:first-child) {
    border-top: none;
  }

  @media (max-width: $breakpoint-tablet) {
    padding-bottom: 44px;
  }

  @media (max-width: $breakpoint-mobile) {
    padding-top: 14px;
    padding-bottom: 21px;
    padding-right: 20px;
    padding-left: 13px;
  }

  & p {
    margin: 0;
    padding: 0 12px;
    font-weight: normal;
    width: 60%;
    display: none;
    margin-top: 8px;

    @media (max-width: $breakpoint-tablet) {
      width: 100%;
      margin-top: 23px;
    }

    @media (max-width: $breakpoint-mobile) {
      font-size: 14px;
      line-height: 20px;
      margin-top: 15px;
      padding-right: 34px;
    }
  }
}

.faq__item--show {
  & p {
    display: block;
  }

  & .faq__btn {
    &::after {
      top: 10px;
      transform: rotate(180deg);

      @media (max-width: $breakpoint-tablet) {
        top: 5px;
      }
    }
  }
}

.faq__btn {
  @include main-font-source(18px, 500, 23px, $main-black);
  display: block;
  text-align: left;
  position: relative;
  width: 100%;
  cursor: pointer;

  border: none;
  background: transparent;

  @media (max-width: $breakpoint-tablet) {
    padding-left: 10px;
  }

  @media (max-width: $breakpoint-mobile) {
    font-size: 16px;
    line-height: 22px;
    padding-right: 32px;
  }

  &::after {
    @include pseudo(" ", absolute, 25px, 13px);
    right: 10px;
    top: 10px;
    background-image: url("../img/icon-down.svg");
    background-repeat: no-repeat;

    @media (max-width: $breakpoint-tablet) {
      right: 12px;
      top: 5px;
      background-size: 39px 22px;
      background-position: 50% 50%;
    }

    @media (max-width: $breakpoint-mobile) {
      right: 0;
      top: 5px;
    }
  }
}
