.footer {
  position: relative;
  flex-grow: 0;
  background-color: $black;
  bottom: 0;

  & input {
    @include main-font-source(14px, 500, 21px, $grey);
    box-sizing: border-box;
    overflow: hidden;
    width: 100%;
    padding: 14px 18px 11px 0;
    border: none;
    background-color: transparent;
    outline: none;

    &:hover,
    &:focus {
      opacity: 0.8;
      background-color: $white;
      color: $grey;
    }
  }

  & button {
    @include main-font-source(16px, 500, 21px, $grey);
    display: block;
    width: 109px;
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;

    &:hover,
    &:focus {
      opacity: 0.9;
    }
  }
}

.footer__container {
  display: flex;
  justify-content: space-between;
  padding-top: 90px;
  padding-bottom: 72px;

  @media (max-width: $breakpoint-tablet) {
    flex-direction: column;
    padding-top: 50px;
    padding-bottom: 35px;
  }

  @media (max-width: $breakpoint-mobile) {
    padding-bottom: 60px;
  }
}

.footer__first-part {
  width: 43%;
  margin-right: 7.5%;

  @media (max-width: $breakpoint-tablet) {
    width: 83%;
    margin-right: auto;
    margin-bottom: 43px;
  }

  @media (max-width: $breakpoint-mobile) {
    width: 100%;
    margin-bottom: 47px;
  }

  & p {
    @include font-source(34px, bold, 44px, $white);
    @include clear(11px);
    letter-spacing: 0.03em;
  }
}

.footer__promise {
  display: block;
  font-weight: normal;
  color: $grey;
  margin-bottom: 27px;

  @media (max-width: $breakpoint-mobile) {
    padding-right: 52px;
  }
}

.footer__inner {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid $white;
}

.footer__email {
  width: 70%;
}

.footer__submit {
  display: flex;

  & span {
    margin-right: 13px;
    color: $white;
  }

  & svg {
    color: $white;
    transform: rotate(180deg);
  }
}

.footer__second-part {
  margin-top: 7px;
  display: flex;
  width: 49.5%;
  justify-content: space-between;

  @media (max-width: $breakpoint-tablet) {
    width: 84%;
    margin-right: auto;
    margin-top: 0;
  }

  @media (max-width: $breakpoint-mobile) {
    flex-direction: column;
    width: 100%;
  }
}
